.App {
  text-align: center;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.title-logo {
  margin: 65px 0 15px 0;
}

.App-link {
  color: #61dafb;
}

.gameCanvas {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%);
}

.victoryStr {
  position: absolute;
  left: 30px;
  top: 30px;
  font-size: 24px;
  color: #fff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
